<template>
  <div class="personal-content-body">
    <div class="content investProjectManageDetail">
      <div class="content-title clear">
        <div class="left">项目管理</div>
      </div>
      <div class="content-body clear">
        <div class="content-body-left-box left">
          <!-- 导航栏 -->
          <div class="text-nav">
            <el-breadcrumb separator-class="el-icon-arrow-right">
              <el-breadcrumb-item :to="{ name: 'directInvestProjectView' ,query: {id: this.projId}}">返回项目首页</el-breadcrumb-item>
              <el-breadcrumb-item>{{companyName}}</el-breadcrumb-item>
            </el-breadcrumb>
          </div>
          <div class="img-nav clear">
            <div class="navbox">
              <div class="nav-item " v-for="(imgnavListItem,imgnavListIndex) in navList.child" :key='imgnavListIndex' :class="{current:imgnavListItem.integrity!='0%'||imgnavListItem.id==moduleId}" @click="toConfirm(imgnavListItem.id)">
                &nbsp;<i class="icon-check" v-if="imgnavListIndex!=navList.child.length-1"></i>
                <span class="triangle"></span>
                <span v-if="navList.child.length>5" style="color:#fff">{{imgnavListItem.name|texteclipsis(8)}}</span>
                <span v-else style="color:#fff">{{imgnavListItem.name}}</span>
              </div>
            </div>
          </div>
          <div class="line"></div>
          <div class="progress-bar-container">
            <progress-bar :schedule="percent"></progress-bar>
            <div class="progress-bar-ts">信息完成度</div>
          </div>
          <div class="content-type-box " v-for="(item,index) in DeatailData" :key="index">
            <div class="content-type-title">{{item.zhName}}
              <div class="content-type-rate-box" v-if="item.grade==1">
                <el-rate :max="5" :colors="['#F7BA2A','#F7BA2A','#F7BA2A']" allow-half show-score v-model="item.star" text-color="#F7BA2A" class="content-type-rate" @change='getPercent'></el-rate>
              </div>
            </div>
            <div class="upload-lists" v-if="item.control==10">
              <div class="upload-list clear" v-for="(uploadPath,uploadName) in item.contents" :key="uploadName">
                <div class="upload-list-name">
                  {{uploadName}}
                </div>
                <span class="right uploadDelete" @click="toDelFundFile(item.id,uploadName)">删除</span>
                <a class="formgroup-file-item-btn right">
                  <input class="formgroup-file-item-btn-inputfile" type="file" multiple="multiple" @change="changefile(item.id,uploadName,$event)">替换</a>
              </div>
              <!-- <div class="formgroup-file-item formgroup-file-item-add">
              <input class="formgroup-file-item-add-inputfile" type="file" multiple="multiple" @change="uploadfile(item.id,$event)">
              <i class="el-icon-plus"></i> 添加文件
            </div> -->
              <el-upload class="upload-demo" drag :data="{id:item.id}" action="" multiple :auto-upload="true" :show-file-list="false" :http-request="uploadfileRequest">
                <i class="el-icon-upload"></i>
                <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
              </el-upload>
            </div>
            <div class="content-type-textarea" v-if="item.control==1">
              <el-input type="textarea" v-model="item.contents" resize='none' @change='getPercent'></el-input>
            </div>
            <el-radio-group v-model="item.contents" class="content-type-radio" v-if="item.control==4" @change='getPercent'>
              <el-radio :label="radioitem" v-for="(radioitem,radioindex) in item.elsed" :key="radioindex">{{radioitem}}</el-radio>
            </el-radio-group>
            <!-- child -->
            <div v-for="(childItem,index2) in item.child" :key="index2">
              <!--child ts  -->
              <div class="content-type-ts">{{childItem.zhName}}
                <div class="content-type-rate-box" v-if="childItem.grade==1">
                  <el-rate :max="5" :colors="['#F7BA2A','#F7BA2A','#F7BA2A']" allow-half show-score v-model="childItem.star" text-color="#F7BA2A" class="content-type-rate" @change='getPercent'></el-rate>
                </div>
              </div>
              <div class="content-type-textarea" v-if="childItem.control==1">
                <el-input type="textarea" v-model="childItem.contents" resize='none' @change='getPercent'></el-input>
              </div>
              <div class="upload-lists" v-if="childItem.control==10">
                <div class="upload-list clear" v-for="(childUploadPath,childUploadName) in childItem.contents" :key="childUploadName">
                  <div class="upload-list-name">
                    {{ childUploadName}}
                  </div>
                  <span class="right uploadDelete" @click="toDelFundFile(childItem.id,childUploadName)">删除</span>
                  <a class="formgroup-file-item-btn right">
                    <input class="formgroup-file-item-btn-inputfile" type="file" multiple="multiple" @change="changefile(childItem.id,childUploadName,$event)">替换</a>
                </div>
                <!-- <div class="formgroup-file-item formgroup-file-item-add">
                <input class="formgroup-file-item-add-inputfile" type="file" multiple="multiple" @change="uploadfile(childItem.id,$event)">
                <i class="el-icon-plus"></i> 添加文件
              </div> -->
                <el-upload class="upload-demo" drag :data="{id:childItem.id}" action="" multiple :auto-upload="true" :show-file-list="false" :http-request="uploadfileRequest">
                  <i class="el-icon-upload"></i>
                  <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
                </el-upload>
              </div>
              <el-radio-group v-model="childItem.contents" class="content-type-radio" v-if="childItem.control==4" @change='getPercent'>
                <el-radio :label="childradioitem" v-for="(childradioitem,childradioindex) in childItem.elsed" :key="childradioindex">{{childradioitem}}</el-radio>
              </el-radio-group>
            </div>
          </div>
          <div class="content-confirm content-type-box ">
            <button @click="toConfirm" v-if="nextMduleId!=null">下一步</button>
            <button @click="toConfirm" v-else>提交</button>
          </div>
        </div>
        <!-- 右侧 -->
        <div class="content-body-right-box right">
          <div class="content-body-right-header">
            <span class="icon-save"></span>
            <span class="header-name">版本记录</span>
          </div>
          <div class="scroll-box">
            <div class="history-null-data" v-if="historyList.length==0">
              暂无提交记录
            </div>
            <div class="content-body-right-items-box" v-else>
              <div class="item-box" v-for="(item,index) in historyList" :key="index" @click="getmoduleDetail(item.id)">
                <div class="item-time"><span class="blue-point"></span>{{item.addTime}}</div>
                <div class="item-text">{{item.name}}提交新版本</div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import {
  moduleDetail,
  moduleDetailUpload,
  saveModuleInfo,
  projOutline
} from '../../config/api'
import progressBar from '../../components/progressbar.vue'
import {
  dialogConfirm,
  dialogConfirmHasbtn,
  getFileTypeMutil
} from '../../config/mUtils'
import index from 'vue';
export default {
  data() {
    return {
      // allnumber:0,//所有的可选项的个数
      // isChangerNum:0,//修改过的内容的个数
      percent: '0%', //显示的百分比
      moduleId: null, //当前页面的模块id.
      nextMduleId: null,
      moduleName: null,
      navList: [], //导航的数据
      DeatailData: [], //当前页面显示的数据
      historyList: [], //历史侧边栏数据
      projId: null, //项目id
      companyName: null,
      saveInterval: null
    }
  },
  components: {
    progressBar
  },
  // computed:{
  //     isLast: function() {
  //         let length = this.navList.length;
  //         console.log(this.navList);
  //         // this.navList[]
  //         // if(this.moduleId)
  //     }
  // },
  mounted() {
    let params = this.$route.query;
    this.moduleId = params.id;
    this.projId = 185;
    this.getHomedataList();
    // this.getmoduleDetail();
    let _that = this
    this.saveInterval = setInterval(function() {
      // _that.savePageData();
      // console.log(1111);
      localStorage.setItem(_that.projId + '&' + _that.moduleId, JSON.stringify(_that.DeatailData));
    }, 30000)
  },
  // watch: {
  //     $route(to, from) {
  //         debugger;
  //         console.log(to);
  //         // console.log(111);
  //         console.log()
  //         if(to.name!='investProjectManageDetail'){
  //             // .clear();
  //             clearInterval(this.saveInterval);
  //         } 
  //     }
  // },
  destroyed() {
    // alert(1111);
    window.clearInterval(this.saveInterval);
  },
  methods: {
    // savePageData(){

    // //    let saveObj = {
    // //        moduleId:this.moduleId,
    // //        projId:this.projId,
    // //        saveData:this.DeatailData,
    // //    }
    // //    console.log(saveObj);
    //    localStorage.setItem(this.projId+'&'+this.moduleId,JSON.stringify(this.DeatailData));
    // },
    //获取该模块详情
    getmoduleDetail(id) {
      let params = {
        projId: this.projId,
        moduleId: this.moduleId,
      }
      if (id != undefined) {
        params.hid = id
      }
      moduleDetail(params).then(res => {
        if (res.data.msgNo == 1) {
          let data = res.data.payload;
          for (let item of data.data) {
            if (item.control == 10) {
              if (item.contents != null && item.contents != '') {
                item.contents = JSON.parse(item.contents)
              }
            }
            if (item.control == 4) {
              item.elsed = item.elsed.split(',');
            }
            if (item.star == null) {
              item.star = 0
            }
            if (item.child != undefined && item.child.length > 0) {
              for (let childItem of item.child) {
                if (childItem.control == 10) {
                  if (childItem.contents != null && childItem.contents != '') {
                    childItem.contents = JSON.parse(childItem.contents)
                  }
                }
                if (childItem.control == 4) {
                  childItem.elsed = childItem.elsed.split(',');
                }
                if (childItem.star == null) {
                  childItem.star = 0;
                }
              }
            }
          }

          let saveData = localStorage.getItem(this.projId + '&' + this.moduleId);
          // console.log(saveData);
          // console.log(id);
          if (id != undefined) {
            //  console.log('记录')
            this.DeatailData = data.data;
          } else {
            if (saveData != null) {
              // console.log('草稿')
              this.DeatailData = JSON.parse(saveData);
            } else {
              //  console.log('数据')
              this.DeatailData = data.data;
            }
          }

          this.historyList = data.history;
          let number = 0;
          // this.nextMduleId = null;
          // console.log(this.navList.child);
          if (this.moduleId != this.navList.child[this.navList.child.length - 1].id) {
            for (let item of this.navList.child) {
              if (item.id == this.moduleId) {
                this.nextMduleId = this.navList.child[number + 1].id;
              }
              number++;
            }
          } else {
            // this.nextMduleId = JSON.parse(JSON.stringify(this.moduleId));
            this.nextMduleId = null;
          }

          this.getPercent();
        }
      })
    },
    //统计处有内容的百分比
    getPercent() {
      // console.log(1111);
      let isChangerNum = 0;
      let allnumber = 0;
      // console.log(this.DeatailData);
      for (let item of this.DeatailData) {
        if (item.control != 0) {
          if (item.contents != null && item.contents != '') {
            // console.log('一级');
            // console.log(typeof item.contents);
            isChangerNum++;
          }
          allnumber++;
          if (item.grade == 1) {
            allnumber++;
            // console.log(1111);
            // console.log(item.star);
            if (item.star != 0 && item.star != null && item.star != '') {
              isChangerNum++;
            }
          }
        }
        // console.log('二级内容');
        // console.log(item.child);
        if (item.child != undefined) {
          //console.log(item.child);
          for (let childItem of item.child) {
            //console.log(childItem);
            if (childItem.control != 0) {
              // console.log(11111);
              // console.log(typeof childItem.contents);
              if (childItem.contents != null && childItem.contents != '') {
                // console.log('二级');
                // console.log(childItem.contents);
                isChangerNum++;
              }
              allnumber++;
              if (childItem.grade == 1) {
                allnumber++;
                if (childItem.star != 0 && childItem.star != null && childItem.star != '') {
                  isChangerNum++;
                }
              }
            }

          }
        }
      }
      if (allnumber == 0) {
        this.percent = '0%'
      } else {
        // console.log((isChangerNum / allnumber) * 100);
        this.percent = parseInt((isChangerNum / allnumber) * 100) + '%'
        // console.log(this.percent);
      }

    },
    //去提交
    toConfirm(navItemId) {
      // console.log( typeof navItemId)
      this.getPercent();
      // console.log(navItemId);
      // console.log(this.moduleId);
      // console.log(this.navList);
      if (this.navList.child != undefined) {
        this.navList.child.forEach(item => {
          // console.log(item);
          if (item.id == this.moduleId) {
            item.integrity = this.percent;
          }
        });
      }

      let newArr = [];
      let copyDeatailData = JSON.parse(JSON.stringify(this.DeatailData));

      for (let item of copyDeatailData) {
        if (item.control == 4) {
          item.elsed = item.elsed.join(',');
        }
        if (item.child != undefined && item.child.length > 0) {
          for (let childItem of item.child) {
            if (childItem.control == 4) {
              childItem.elsed = childItem.elsed.join(',');
            }
            newArr.push(childItem);
          }
          delete item.child;
        }
        newArr.push(item);
      };
      // console.log(copyDeatailData);
      // console.log(this.DeatailData);
      let newObj = {};
      newObj[this.moduleName] = this.percent
      // console.log(newObj);
      // console.log(newArr);
      saveModuleInfo({
        projId: this.projId,
        moduleId: this.moduleId,
        data: JSON.stringify(newArr),
        integrity: JSON.stringify(newObj)
      }).then(res => {
        if (res.data.msgNo == 1) {
          //清空草稿
          localStorage.removeItem(this.projId + '&' + this.moduleId);
          let navListArr = this.navList.child;
          if (this.moduleId == navListArr[navListArr.length - 1].id) {
            if (typeof navItemId == 'number') {
              this.goOtherModule(navItemId);
            } else {
              dialogConfirm('success', '提交成功')
              this.$router.push({
                path: '/home/investProjectManageHomepage',
                query: { id: this.projId }
              })
            }

          } else {
            if (typeof navItemId == 'number') {
              this.goOtherModule(navItemId);
            } else {
              this.goOtherModule(this.nextMduleId);
            }

          }
        }
      })
    },
    //替换修改文件
    changefile(id, fileName, $event) {
      let files = $event.target.files
      moduleDetailUpload(files).then(res => {
        if (res.data.msgNo == 1) {
          let data = res.data.payload;
          // console.log(data);
          for (let item1 of this.DeatailData) {
            if (item1.id == id) {
              let newArr = {};
              for (let i in data) {
                newArr[files[i].name] = data[i]
              }
              let oldArr = JSON.parse(JSON.stringify(item1.contents))
              delete oldArr[fileName];
              // console.log(oldArr);
              let lastArr = Object.assign(oldArr, newArr);
              item1.contents = lastArr;
            } else {
              if (item1.child != undefined && item1.child.length > 0) {
                for (let item2 of item1.child) {
                  if (item2.id == id) {
                    let newArr2 = {};
                    for (let i in data) {
                      newArr2[files[i].name] = data[i]
                    }
                    let oldArr = JSON.parse(JSON.stringify(item2.contents));
                    delete oldArr[fileName];
                    // console.log(oldArr);
                    let lastArr = Object.assign(oldArr, newArr2);
                    item2.contents = lastArr;
                  }
                }
              }
            }
          }
          this.getPercent();
        }
        $event.target.value = '';
      })

    },

    //上传文件 
    uploadfile(id, $event) {
      // console.log(id);
      let files = $event.target.files
      moduleDetailUpload(files).then(res => {
        if (res.data.msgNo == 1) {
          let data = res.data.payload;
          for (let item1 of this.DeatailData) {
            if (item1.id == id) {
              let newArr = {};
              for (let i in data) {
                newArr[files[i].name] = data[i]
              }
              let oldArr = JSON.parse(JSON.stringify(item1.contents))
              // console.log(oldArr);
              if (oldArr == null || oldArr == '') {
                oldArr = {};
              }
              let lastArr = Object.assign(oldArr, newArr);
              item1.contents = lastArr;
              // console.log(item1.contents);
            } else {
              if (item1.child != undefined && item1.child.length > 0) {
                for (let item2 of item1.child) {
                  if (item2.id == id) {
                    let newArr2 = {};
                    for (let i in data) {
                      newArr2[files[i].name] = data[i]
                    }
                    let oldArr = JSON.parse(JSON.stringify(item2.contents))
                    if (oldArr == null || oldArr == '') {
                      oldArr = {};
                    }
                    let lastArr = Object.assign(oldArr, newArr2);
                    item2.contents = lastArr;
                  }
                }
              }
            }
          }
          this.getPercent();
        }
        $event.target.value = '';
      })
    },
    uploadfileRequest($event) {
      let id = $event.data.id;
      let files = [];
      let type = getFileTypeMutil($event.file.name);
      if (type == "") {
        dialogConfirm('info', '不支持该类型文件上传');
        return;
      } else {
        files.push($event.file);
      }
      moduleDetailUpload(files).then(res => {
        if (res.data.msgNo == 1) {
          let data = res.data.payload;
          // console.log(this.DeatailData);
          for (let item1 of this.DeatailData) {
            if (item1.id == id) {
              let newArr = {};
              for (let i in data) {
                newArr[files[i].name] = data[i]
              }
              let oldArr = JSON.parse(JSON.stringify(item1.contents))
              // console.log(oldArr);
              if (oldArr == null || oldArr == '') {
                oldArr = {};
              }
              let lastArr = Object.assign(oldArr, newArr);
              item1.contents = lastArr;
              // console.log(item1.contents);
            } else {
              if (item1.child != undefined && item1.child.length > 0) {
                for (let item2 of item1.child) {
                  if (item2.id == id) {
                    let newArr2 = {};
                    for (let i in data) {
                      newArr2[files[i].name] = data[i]
                    }
                    let oldArr = JSON.parse(JSON.stringify(item2.contents))
                    if (oldArr == null || oldArr == '') {
                      oldArr = {};
                    }
                    let lastArr = Object.assign(oldArr, newArr2);
                    item2.contents = lastArr;
                  }
                }
              }
            }
          }
          this.getPercent();
        }
        //$event.target.value = '';
      })
    },
    //删除文件列表信息
    toDelFundFile(id, fileName) {
      // console.log(item1[item2]);
      // console.log(id);
      // console.log(fileName);
      for (let item1 of this.DeatailData) {
        if (item1.id == id) {
          // console.log(item1.contents);
          let newObj = {};
          newObj = JSON.parse(JSON.stringify(item1.contents));
          delete newObj[fileName];
          // console.log(Object.keys(newObj).length);
          if (Object.keys(newObj).length == 0) {
            item1.contents = null;
          } else {
            item1.contents = newObj;
          }

        } else {
          if (item1.child != undefined && item1.child.length > 0) {
            for (let item2 of item1.child) {
              if (item2.id == id) {
                // console.log(item2.contents);
                let newObj2 = {};
                newObj2 = JSON.parse(JSON.stringify(item2.contents));
                delete newObj2[fileName];
                if (Object.keys(newObj2).length == 0) {
                  item2.contents = null;
                } else {
                  item2.contents = newObj2;
                }
              }
            }
          }
        }
      }
      this.getPercent();
      // console.log(this.DeatailData[0].contents);
    },

    // goBackHome() {
    //     console.log(111);
    //     this.$router.push({
    //         path: '/home/investProjectManageHome',
    //         query: {
    //             id: this.projId,
    //         }
    //     })
    // },
    goOtherModule(moduleId) {
      this.moduleId = moduleId;
      for (let item of this.navList.child) {
        if (item.id == this.moduleId) {
          this.moduleName = item.name;
        }
      };
      // this.getHomedataList();
      for (let item1 of this.HomedataList) {
        if (item1.child.length > 0) {
          for (let i of item1.child) {
            if (i.id == this.moduleId) {
              this.moduleName = i.name;
              this.navList = item1;
            }
          }
        }

      };
      this.getmoduleDetail();
      this.$router.push({
        path: '/home/investProjectManageDetail',
        query: {
          id: moduleId,
          projId: this.projId,
        }
      })
    },
    // getNavModuleInfo(moduleId){

    // },
    //获取项目详情
    getHomedataList() {
      projOutline({
        projId: this.projId,
      }).then(res => {
        if (res.data.msgNo == 1) {
          let data = res.data.payload.data;
          if(res.data.payload.name){
            this.companyName = res.data.payload.name
          }else{
            this.companyName = '项目名称'
          }
          
          this.HomedataList = res.data.payload.data;
          // console.log(data)
          for (let item of data) {
            if (item.child.length > 0) {
              for (let i of item.child) {
                if (i.id == this.moduleId) {
                  this.moduleName = i.name;
                  this.navList = item;
                }
              }
            }

          };
          this.getmoduleDetail();
        }
      })
    },
    //通过id遍历数组修改上传文件信息

  }
};

</script>
<style lang="scss" scoped>
