  max-width: 1160px;
  min-width: 928px;
  border: 1px solid #E4E4E4; // margin-top: 12px;
  padding: 30px 0px 100px 18px;
  color: #35383A;
} //左边容器
.content-body-left-box {
  // position: absolute;
  // left: 18px;
  // bottom: 10px;
  // top: 30px;
