//导航栏样式
.navbox {
  display: flex;
  width: 590px;
  flex-wrap: nowrap;
}

.nav-item {
  flex: 1; //margin-left: -10px;
  position: relative;
  line-height: 26px;
  background: #eaeaea;
  text-align: center;
  height: 26px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  color: #fff;
  font-size: 13px;
  &:after {
    content: '';
    z-index: 99;
    position: absolute;
    right: -11px;
    width: 0px;
    top: -2px;
    bottom: 0px;
    height: 0px;
    display: block;
    border-top: 15px solid transparent;
    border-left: 12px solid #fff;
    border-bottom: 15px solid transparent;
  }
  .triangle {
    z-index: 100;
    position: absolute;
    right: -8px;
    width: 0px;
    height: 0px;
    top: 0px;
    display: block;
    border-top: 13px solid transparent;
    border-left: 10px solid #eaeaea;
    border-bottom: 13px solid transparent;
  }
  cursor: pointer;
}

.nav-item.current {
  background: #87bcfe;
  .triangle {
    border-left-color: #87bcfe;
  }
}

.nav-item:nth-child(2).current {
  background: #5EA9FF;
  .triangle {
    border-left-color: #5EA9FF;
  }
}

.nav-item:nth-child(3).current {
  background: #469CFF;
  .triangle {
    border-left-color: #469CFF;
  }
}

.nav-item:nth-child(4).current {
  background: #2F90FF;
  .triangle {
    border-left-color: #2F90FF;
  }
}

.nav-item:nth-child(5).current {
  background: #4C91E0;
  .triangle {
    border-left-color: #4C91E0;
  }
}

.nav-item:nth-child(6).current {
  background: #3B7BC5;
  .triangle {
    border-left-color: #3B7BC5;
  }
}

.nav-item:nth-child(7).current {
  background: rgb(54, 118, 192);
  .triangle {
    border-left-color: rgb(54, 118, 192);
  }
}

.nav-item:first-child {
  border-radius: 13px 0px 0px 13px;
}

.nav-item:last-child {
  border-radius: 0px 13px 13px 0px;
  .triangle {
    display: none;
  }
  &:after {
    display: none;
  }
}

